import React, { Component } from 'react';
import 'antd/dist/antd.css';
import axios from 'axios';
// ant
import { Carousel } from 'antd';
import qs from 'qs';
class Xbanner extends Component {
    constructor() {
        super();
        this.state = {
            inputTxt: '',
            inputNum: ''
        }
    }
    // 
    changeUser(e) {
        if (e.target.className == 'input_txt input_user') {
            this.setState({
                inputTxt: e.target.value,
            })
        } else if (e.target.className == 'input_txt input_phone') {
            this.setState({
                inputNum: e.target.value,
            })
        }
    }
    // 
    clickMessage(e) {
        e.preventDefault();
        if (this.state.inputTxt.trim().length == 0 || this.state.inputNum.trim().length == 0) {
            alert('');
            return;
        }
        if (/^1[3-8]\d{9}$/.test(this.state.inputNum)) {
            axios.post('http://localhost:3000/users/insertuser', qs.stringify(
                {
                    user: this.state.inputTxt.slice(0,4),
                    phone: this.state.inputNum
                }
            )).then((data) => {
                if(data.data.status=='success'){
                    alert('');
                    this.setState({
                        inputTxt:'',
                        inputNum:''
                    },()=>{
                        this.refs.inputtxt.focus();
                    })
                }
            }).catch((err) => {
                console.log(err);
            })
        }else{
            alert('');
            this.setState({
                inputTxt:'',
                inputNum:''
            },()=>{
                this.refs.inputtxt.focus();
            })
        }
    }
    render() {
        return (
            <div className="banner">
                <Carousel autoplay>
                    <div><img src="http://localhost:3000/images/shouye/20171211215950_82733.png" alt="" /></div>
                    <div><img src="http://localhost:3000/images/shouye/20171227143731_89875.jpg" alt="" /></div>
                </Carousel>
                <div className="usertable">
                    <form className="yuyueform">
                        <div className="title_wrap"></div>
                        <div className="input_wrap input_name">
                            <input type="text" className="input_txt input_user" ref="inputtxt" placeholder="" onChange={this.changeUser.bind(this)} value={this.state.inputTxt} />
                        </div>
                        <div className="input_wrap input_phonenum">
                            <input ref="inputNum" type="text" className="input_txt input_phone" onChange={this.changeUser.bind(this)} placeholder="" value={this.state.inputNum} />
                        </div>
                        <div className="button_content">
                            <button onClick={this.clickMessage.bind(this)}></button>
                        </div>
                    </form>
                </div>
                {/*  */}
                <div className="total">
                    <div className="total_detail con">
                        <dl className="dl1">
                            <dt>  </dt>
                            <dd> <span id="leijiKaihu">548061</span>  </dd>
                        </dl>
                        <dl className="dl2">
                            <dt>  </dt>
                            <dd> <span id="leijiFanyng">216280921</span>  </dd>
                        </dl>
                        <dl className="dl3">
                            <dt>  </dt>
                            <dd> <span id="saft_year">8</span>  <span id="saft_day">6</span>  <span id="saft_hour">9</span>  <span id="saft_min">15</span>  <span id="saft_sec">53</span>  </dd>
                        </dl>
                        <dl className="dl4">
                            <dt>  </dt>
                            <dd><span id="zuoriFanyong">1416617</span>  </dd>

                        </dl>
                    </div>
                </div>
            </div>
        )
    }
}
export default Xbanner;